@import url(https://fonts.googleapis.com/css2?family=Stint+Ultra+Expanded&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap);
:root {
  --red: #F38181;
  --green: #FCE38A;
  --light-green: #EAFFD0;
  --teal: #95E1D3;
  
  --dark-text: rgb(59, 59, 59);
  --mediumLight-text: rgb(112, 112, 112);
  --light-text: rgb(241, 236, 236);

  --title-font: 'Stint Ultra Expanded', cursive;
  --default: 'Raleway', sans-serif;
}

*, *::before, *::after {
  box-sizing: border-box;
}

*{
  margin: 0;
}

html, body {
  height: 100%;
  font-size: 16px;
}

body{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}


#root {
  width: 100%;
  height: 100%;
}


